.items {
  padding: 25px 0px 25px 25px;
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
}
.item {
  background-color: #64748b;
  color: white;
  border-radius: 15px;
  height: 300px;
  width: 450px;
  display: flex;
  padding: 5px;
}
.img {
  height: 300px;
  width: 200px;
}
.item:hover {
  background-color: #475569;
}
